button{
  min-height: 40px;
  width: 40px;
  padding: -5px 20px;
  background-color: #8566aa;
  color: #fff;
  font-size: 40px;
  border:none;
  border-radius: 50%;
  text-transform: uppercase;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}

button:hover{
  background-color: #20bf6b;
}


*{
  box-sizing:border-box;
  padding: 0;
  margin: 0;
  font-family:"Josefin Sans","Arial Narrow";
}
.header{
  width: 100%;
  line-height: 100px;
  background-color:#f5ba13 ;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-left: 50px;
  color: white;
  box-shadow :5px 3px 15px -5px rgba(0,0,0,0.3)
}
.note{
  background:#fff ;
  border-radius:7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;
}
.note .btn:hover {
  background-color: #f5ba13;
}
.note .btn:hover .deleteIcon{
  background-color: #fff;
}


.note .btn{
  position: relative;
  float: right;
  margin-right: 10px;
  color:#f4b400;
  border:none;
  width: 50px;
  height: 50px;
  background:transparent;
  cursor: pointer;
  outline: none;
  box-shadow: 3px 3px 15px -10px rgba(253, 195, 195, 0.5);
}
.note p{
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
.note h1{
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
.MuiButton-root{
  width: 30px;
  height: 50px;
  position:absolute;
  bottom: -28px;
  left:400px;
  cursor: pointer;
  background-color: #fff !important;

}
.main_notes{
  width: 100px;
  padding: 15px 10px 5px 10px;
  margin: 20px auto;
}
my_notes h1{
  color: #34495e;
}
.my_notes p{
  color:grey;
}
form{
  min-height: 50px;
  color:#ecf0f1;
  padding:15px 10px 5px 10px;
  box-shadow: 3px 5px 15px -5px rgba(0,0,0,0.3);
  border-radius: 10px;
  position: relative;
}
input{
  width: 100%;
  line-height: 30px;
  background: transparent;
  border:none;
  outline: none;
  font-size: 2rem;
  margin: 10px 0;
  font-weight: bold;
}
textarea{
  width: 100%;
  line-height: 30px;
  background:transparent;
  border:none;
  outline: none;
  font-size:1rem;
  margin:10px 0;
}
.MuiButton-root:hover .plus_sign{
  color:#fff;
  background:transparent;
}

.MuiButton-root .plus_sign{
   color:#f4b400;
  cursor: pointer;
  z-index: 222;
  font-size: 45px  !important;
  font-weight: 900;
 background: transparent;
}

.MuiButton-root:hover{
  background: #f4b400;
}

.deleteIcon{
  width: 0.7em;
  height: 0.7em;
}



.footer{
  width:100%;
  position: absolute;
  bottom: 0;

}